<template>
  <div class="checkbox-view">
    <el-checkbox
        :indeterminate="isIndeterminate"
        v-model="checkAll"
        @change="handleCheckAllChange"
        >全选</el-checkbox
    >
    <div style="margin:0 10px"></div>
    <el-checkbox-group
        v-model="checkedCities"
        @change="handleCheckedCitiesChange"
    >
        <el-checkbox v-for="city in cities" :label="city" :key="city">
        {{city}}
        </el-checkbox>
    </el-checkbox-group>
  </div>
</template>
<script>
const cityOptions = ["上海", "北京", "广州", "深圳", "杭州"];
export default {
  data() {
    return {
      checkAll: false,
      checkedCities: ["杭州"],
      cities: cityOptions,
      isIndeterminate: true,
    };
  },
  methods: {
    handleCheckAllChange(val) {
      this.checkedCities = val ? cityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    },
  },
};
</script>
<style scoped>
/*  
  样式穿透（后两种需要scss支持）
  >>>  
  :deep()  
  /deep/    
*/
.checkbox-view {
    display: flex;
    padding: 30px;
}

/* checkbox */
.checkbox-view >>> .el-checkbox__input.is-checked .el-checkbox__inner {
  background-color: #f50537;
  border-color: #f50537;
}
.checkbox-view >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #f50537;
  border-color: #f50537;
}
.checkbox-view >>> .el-checkbox__inner {
  width: 24px;
  height: 24px;
  border-radius: 2px;
  font-size: 18px;
  background: #fff;
  border-color: #dcdfe6;
}

/* label样式 */
:deep() .el-checkbox__label ,
:deep() .el-checkbox__input.is-checked+.el-checkbox__label {
    color: #1A1A1A;
}

/* focus border-color */
:deep() .el-checkbox__input.is-focus .el-checkbox__inner {
  border-color: #f50537;
}

/* 勾选状态：对勾样式 */
:deep()  .el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #fff;
  border-color: #fff;
}
:deep()  .el-checkbox__inner::after {
  border: 2px solid #fff;
  border-left: 0px;
  border-top: 0px;
  width: 5px;
  height: 9px;
  left: 8px;
  position: absolute;
  top: 4px;
}

/* 半选中对勾样式 */
:deep() .is-indeterminate .el-checkbox__inner::before  {
    position: absolute;
    top: 10px!important;
    border: 1px solid #fff;
}
/* checkbox hover */
:deep()  .el-checkbox__inner:hover {
  border-color: #f50537;
}

</style>
